//const API_KEY = process.env.PARCEL_API_KEY;

const API_URL_BOOK1 = 'https://api.nytimes.com/svc/books/v3/reviews.json?author=Stephen+King&api-key=OFLLqsGZSsAo17IU6V6M891n9cVBxYSA';
const API_URL_TOP = 'https://api.nytimes.com/svc/topstories/v2/home.json?api-key=OFLLqsGZSsAo17IU6V6M891n9cVBxYSA';
const API_URL_POP = 'https://api.nytimes.com/svc/mostpopular/v2/viewed/1.json?api-key=OFLLqsGZSsAo17IU6V6M891n9cVBxYSA}';
const API_URL_BOOK = 'https://api.nytimes.com/svc/books/v3/lists/current/hardcover-fiction.json?api-key=OFLLqsGZSsAo17IU6V6M891n9cVBxYSA';

export const fetchAllData = async () => {
    try {
      const [topNewsRes, popularArticlesRes, booksRes, books1Res] = await Promise.all([
        fetch(API_URL_TOP),
        fetch(API_URL_POP),
        fetch(API_URL_BOOK),
        fetch(API_URL_BOOK1),
      ]);
  
      if (!topNewsRes.ok) {
        throw new Error(`HTTP error! status: ${topNewsRes.status}`);
      }
      if (!popularArticlesRes.ok) {
        throw new Error(`HTTP error! status: ${popularArticlesRes.status}`);
      }
      if (!booksRes.ok) {
        throw new Error(`HTTP error! status: ${booksRes.status}`);
      }

      if (!books1Res.ok) {
        throw new Error(`HTTP error! status: ${books1Res.status}`);
      }
  
      const topNewsData = await topNewsRes.json();
      const popularArticlesData = await popularArticlesRes.json();
      const booksData = await booksRes.json();
      const books1Data = await books1Res.json();
  
      return { topNewsData, popularArticlesData, booksData, books1Data };
    } catch (e) {
      console.error('An error occurred while fetching data:', e);
      return {
        topNewsData: { results: [] },
        popularArticlesData: { results: [] },
        booksData: { results: [] },
        books1Data: { results: [] },
      };
    }
  };