import { fetchAllData } from "./js/fetch";

import { updateFeaturedStories, updatePopularArticle, updateBooksSection, updateNewBooksSection, updateNewBooksStephenKingSection} from "./js/updateContent";

import { updateActiveNav } from "./js/activeNav";

(async () => {
  const { topNewsData, popularArticlesData, booksData, books1Data } = await fetchAllData();
  updateFeaturedStories(topNewsData);
  updatePopularArticle(popularArticlesData);
  updateBooksSection(booksData);
  updateActiveNav('home')

  document.getElementById('libro').addEventListener('click', () => {
    updateNewBooksSection(booksData)
    updateNewBooksStephenKingSection(books1Data)
    updateActiveNav('books')

  })
  document.getElementById('home').addEventListener('click', () => {
    updateFeaturedStories(topNewsData);
    updatePopularArticle(popularArticlesData);
    updateBooksSection(booksData);
    updateActiveNav('home')
  })
})();
