const topNews = document.getElementById('top-stories');
const relatedNews = document.getElementById('related-stories');
const booksSection = document.getElementById('books-section-home');
const hardCoverFictionSection = document.getElementById('hardcover-fictions');
const stephenKingSection = document.getElementById('stephen-king');

export const updateFeaturedStories = (data) => {
  topNews.innerHTML = '';
  stephenKingSection.innerHTML = '';
  hardCoverFictionSection.innerHTML = '';
  
  (data.results || []).slice(0, 12).forEach((news) => {
    const newsContainer = document.createElement('div');
    newsContainer.setAttribute('class', 'article');

    const articleImage = news.multimedia?.[0]?.url
      ? `<img src="${news.multimedia[0].url}" alt="${
        news.title
        }" class="article-img" onclick="window.open('${news?.url || '#'}')">`
      : '';
  
    const articleImageTablet = news.multimedia?.[0]?.url
      ? `<img src="${news.multimedia[0].url}" alt="${news.title}"
        class="tablet-img" onclick="window.open('${news?.url || '#'}"')>`
      : '';
  
    const articleImageMobile = news.multimedia?.[0]?.url
      ? `<img src="${news.multimedia[1].url}" alt="${news.title}"
        class="mobile-img" onclick="window.open('${news?.url || '#'}"')>`
      : '';
  
    const articleContent = `
      ${articleImageMobile}
      ${articleImageTablet}
      <div class="article-contents">
        <h3>${news.subsection}
        <h2><a onclick="window.open('${news?.url || '#'}')">${
        news?.title || 'No Title'
      }</a></h2>
        <p>${news?.abstract || 'No Abstract'}</p>
        <button class="read-more" onclick="window.open('${news?.url || '#'}"')>Read More <i class='bx bx-right-arrow-alt'></i>
      </div>
      <div class="additional-content">
      ${articleImage}
      <p>${news.multimedia[1].caption}
      <h4>${news.multimedia[1].copyright}
      </div>
      `;
  
    newsContainer.innerHTML = `${articleContent}`;
    topNews.appendChild(newsContainer);
  });
};
  
export const updatePopularArticle = (data) => {
  relatedNews.innerHTML = '';
  stephenKingSection.innerHTML = '';
  hardCoverFictionSection.innerHTML = '';

  
  (data.results || []).slice(0, 10).forEach((news) => {
    const newsContainer = document.createElement('div');
    newsContainer.setAttribute('class', 'popular-articles');
  
    const popularArticleImage = news.media?.[0]?.['media-metadata']?.[2]?.url
      ? `<img src="${news.media[0]['media-metadata'][2].url}" alt="${
          news?.title || 'No Title'
        }" class="solo-article" onclick="window.open('${news?.url || '#'}')">`
      : '';
  
    const popularArticleContent = `
      <div class="popular-article">
        <h3>${news.section}</h3>
        ${popularArticleImage}
        <h2><a onclick="window.open('${news?.url || '#'}')">${
        news?.title || 'No Title'
      }</a></h2>
        <h4>${news?.byline || 'No Byline'} | ${news.media?.[0]?.copyright || 'No Source'}</h4>
        <p>${news?.abstract || 'No Abstract'}</p>
      </div>
      `;
  
    newsContainer.innerHTML = `${popularArticleContent}`;
    relatedNews.appendChild(newsContainer);
  });
};
  
export const updateBooksSection = (data) => {
  console.log(data);
  booksSection.innerHTML = '';
  stephenKingSection.innerHTML = '';
  hardCoverFictionSection.innerHTML = '';
  
  (data.results.books || []).slice(0, 5).forEach((book) => {
    const booksContainer = document.createElement('div');
    booksContainer.setAttribute('class', 'hardcover-fiction');
  
    const booksImage = `<img src='${book.book_image}' alt="${
      book.title || 'no title'
      } " class="book">`;
    const booksRank = `<h3>${book.rank}</h3>`;
    const booksContent = `<div class="hardcover-fiction-books">
                            <h2>${book.title}</h2>
                            <p>by ${book.author} | ${book.publisher}</p>
                            <p>${book.description}</p>
                            <button id='buylinks' onclick="window.open('${book.amazon_product_url || '#'}')">Buy</button>
                          </div>`;
  
    booksContainer.innerHTML = `${booksRank} ${booksContent} ${booksImage}`;
    booksSection.appendChild(booksContainer);
  });
};

export const updateNewBooksSection = (data) => {
  document.querySelectorAll('.title').forEach(element => {
    element.innerHTML = '';
  });
  topNews.innerHTML = '';
  relatedNews.innerHTML = '';
  booksSection.innerHTML = '';
  hardCoverFictionSection.innerHTML = '';

  const title = document.createElement('h1');
  title.setAttribute('class', 'book-title');
  title.textContent = 'Hard Cover Fiction';
  hardCoverFictionSection.appendChild(title);

  let currentRow = document.createElement('div');
  currentRow.setAttribute('class', 'books-row');
  currentRow.setAttribute('id', 'books-row');

  (data.results.books || []).forEach((book) => {

    const booksNewContainer = document.createElement('div');
    booksNewContainer.setAttribute('class', 'books-section');

    const booksImage = `<img src='${book.book_image}' alt="${book.title || 'no title'}" class="book">`;
    const booksRank = `<h3>${book.rank}.</h3>`;
    const booksContent = `
      <div class="books-section-content">
        <h2>${book.title}</h2>
        <p>by ${book.author} | ${book.publisher}</p>
        <p>${book.description}</p>
        <button id='buylinks' onclick="window.open('${book.amazon_product_url || '#'}')">Buy</button>
      </div>`;

    booksNewContainer.innerHTML = `${booksRank} ${booksContent} ${booksImage}`;

    currentRow.appendChild(booksNewContainer);

    hardCoverFictionSection.appendChild(currentRow);
  });

}

export const updateNewBooksStephenKingSection = (data) =>{
  stephenKingSection.innerHTML = '';

  const title = document.createElement('h1');
  title.setAttribute('class', 'book-title');
  title.textContent = 'Stephen King';
  stephenKingSection.appendChild(title);
  (data.results || []).forEach((book) =>{
    const booksNewStephenKingContainer = document.createElement('div');
    booksNewStephenKingContainer.setAttribute('class', 'stephen-king-section');

    const booksContent = `
      <div class="stephen-king-section-content">
        <h2>${book.book_title}</h2>
        <p>by ${book.book_author} | ${book.byline}</p>
        <p>${book?.summary || 'No Summary'}</p>
        <button id='read-more' onclick="window.open('${book?.url || '#'}')">Read More <i class='bx bx-right-arrow-alt'>
      </div>`;
    
    if (!book.summary == ""){
      booksNewStephenKingContainer.innerHTML = `${booksContent}`;
      stephenKingSection.appendChild(booksNewStephenKingContainer);
    }
  })
}